import React from 'react'
import PropTypes from 'prop-types'

import ReactMarkdown from 'react-markdown'

export const Blog = ({ posts }) =>
  <div>
    { posts.map((post, i) =>
      <ReactMarkdown key={i} source={post} />
    )}
  </div>

Blog.propTypes = {
  posts: PropTypes.array.isRequired
}

export default Blog



